import React, { Component } from "react";
import shortid from "shortid";
import ContactForm from "./ContactForm/ContactForm";
import Filter from "./Filter/Filter";
import ContactList from "./ContactList/ContactList";

const filterContactsByQuery = (filter, contacts) => {
  return contacts.filter(contact =>
    contact.name.toLowerCase().includes(filter.toLowerCase())
  );
};

export default class App extends Component {
  state = {
    contacts: [],
    filter: ""
  };

  addContact = contact => {
    const { name } = contact;
    const contactsArray = this.state.contacts;
    const nameExist = contactsArray.find(con => con["name"] === name);
    if (nameExist) {
      alert(`${name} is already exist`);
      return;
    }

    const contactToAdd = {
      ...contact,
      id: shortid.generate()
    };

    this.setState(state => ({
      contacts: [...state.contacts, contactToAdd]
    }));
  };

  deleteNumber = id => {
    this.setState(state => ({
      contacts: state.contacts.filter(contact => contact.id !== id)
    }));
  };

  changeFilter = e => {
    this.setState({ filter: e.target.value });
  };

  render() {
    const { contacts, filter } = this.state;
    const filteredContacts = filterContactsByQuery(filter, contacts);

    return (
      <div>
        <h1>Phonebook</h1>
        <ContactForm onAddContact={this.addContact} />
        <h2>Contacts</h2>
        {contacts.length >= 2 && (
          <Filter value={filter} onChangeFilter={this.changeFilter} />
        )}
        {contacts.length > 0 && (
          <ContactList
            items={filteredContacts}
            onDeleteNumber={this.deleteNumber}
          />
        )}
      </div>
    );
  }
}
